#App{
  background-color: rgb(1, 1, 36);
  /* background-color: rgb(1, 1, 36); */
  height: 100vh;
  width: 100%;
  
  
}

.innerContainer{
  display: flex;
  align-items: center;
  justify-content: center;
}

#homeLink{
  text-decoration: none;
  color: white;
  padding-left: 2rem;
}



#about, #heading{
  color: white;
}

#heading{
  font-size: 30px;
}

#about{
  font-size: 14px;
  margin-top: 10px;
}

.text-content, .image-section{
  padding: 2rem;
  width: 50%;
  display: flex;
  flex-direction: column;
}

#whitelist-form{
  background-color: black;
  margin-top: 10rem;
  width: 50%;
  padding: 1rem;
  margin-left: 1rem;
}


@media screen and (max-width: 800px) {
  .innerContainer{
    flex-direction: column;
  }
  .text-content, .image-section{
    width: 100%;
    padding: 1.5rem;
  }
